<template>
  <div class="wrapperCenter setPage">
          <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item> 来粉统计 / 控制台 / 个人设置</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form ref="user" :model="user" label-width="80px" :rules="userRules" class="userBox">
      <el-form-item label="用户名">
        <el-input v-model="user.user_name"></el-input>
      </el-form-item>
      <el-form-item label="联系手机" >
        <el-input v-model="user.mobile" disabled></el-input>
      </el-form-item>
      <el-form-item label="联系邮箱">
        <el-input v-model="user.email"></el-input>
      </el-form-item>
    </el-form>
    <el-button type="primary" @click="submit">提交</el-button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user: {},
      userRules: {
        user_name: [
          { required: true, message: "请获取验证码", trigger: "blur" },
        ],
      },
    };
  },
  mounted() {
    this.user = JSON.parse(localStorage.getItem("userInfo")).account_info;
  },
  methods: {
    submit() {
        this.$refs['user'].validate(valid => {
            if(valid){
                this.axios.post("/v1/member/User_setting/index", this.user).then(res => {
                    if(res.data.code == 200){
                        this.$message.success("修改成功")
                        let a = JSON.parse(localStorage.getItem("userInfo"));
                        a.account_info = this.user;
                        localStorage.setItem("userInfo", JSON.stringify(a))
                        location.reload();
                    }else{
                        this.$message.error(res.data.message)
                    }
                })
            }
        })
    },
  },
};
</script>

<style lang="less"scoped>
.el-button{
    margin-left: 20%;
    margin-top: 30px;
}
.userBox {
  width: 30%;
  margin-top: 80px;
  margin-left: 14%;
}
.setPage{
  margin-bottom: 200px;
}
</style>